/* ======== RESET ======== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
  color: #fff;
  background: linear-gradient(-45deg, #0f2027, #203a43, #2c5364, #0f2027);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  overflow-x: hidden;
}

/* Animation du d√©grad√© */
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* ======== HEADER ======== */
header {
  position: sticky;
  top: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(10px);
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
  padding: 15px 0;
  text-align: center;
}

header h1 {
  font-size: 2rem;
  letter-spacing: 1px;
  color: #ffcc00;
}

header h2 {
  font-weight: 400;
  color: #ddd;
  font-size: 1.1rem;
  margin-top: 6px;
}

/* ======== NAV ======== */
.menu {
  margin-top: 10px;
}

.menu ul {
  display: flex;
  justify-content: center;
  list-style: none;
  gap: 25px;
  padding: 0;
}

.menu li {
  cursor: pointer;
  font-weight: 500;
  color: #fff;
  transition: color 0.3s ease, transform 0.2s ease;
  text-transform: uppercase;
  font-size: 0.95rem;
  letter-spacing: 0.5px;
}

.menu li:hover {
  color: #ffcc00;
  transform: scale(1.05);
}

/* ======== SECTIONS ======== */
section {
  max-width: 1000px;
  margin: 80px auto;
  padding: 50px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  backdrop-filter: blur(8px);
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
  scroll-margin-top: 120px;
}

section h3 {
  font-size: 1.6rem;
  color: #ffcc00;
  margin-bottom: 20px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.1);
  padding-bottom: 10px;
  text-align: center;
}

/* ======== LISTES / TEXT ======== */
section ul {
  margin-top: 15px;
  padding-left: 20px;
}

section li {
  margin-bottom: 8px;
  line-height: 1.6;
}

section p {
  line-height: 1.8;
  color: #ddd;
}



/* ======== RESPONSIVE ======== */
@media (max-width: 768px) {
  header h1 {
    font-size: 1.6rem;
  }

  .menu ul {
    flex-wrap: wrap;
    gap: 15px;
  }

  section {
    padding: 30px 20px;
    margin: 50px 10px;
  }
}


.experience {
  position: relative;
  padding: 80px 20px;
  color: #fff;
}

/* === Titre principal === */
.experience h3 {
  text-align: center;
  font-size: 2rem;
  color: #00bcd4;
  text-transform: uppercase;
  margin-bottom: 70px;
  letter-spacing: 1px;
  position: relative;
}

.experience h3::after {
  content: "";
  position: absolute;
  bottom: -12px;
  left: 50%;
  width: 80px;
  height: 3px;
  background: #00bcd4;
  transform: translateX(-50%);
  border-radius: 3px;
}

/* === Ligne centrale de la timeline === */
.experience::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  width: 3px;
  height: 100%;
  background: rgba(0, 188, 212, 0.2);
  border-radius: 10px;
  box-shadow: 0 0 12px rgba(0, 188, 212, 0.3);
}

/* === Carte d'exp√©rience === */
.job {
  position: relative;
  width: 50%;
  padding: 25px 35px;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  margin-bottom: 50px;
  backdrop-filter: blur(6px);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.job:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 20px rgba(0, 188, 212, 0.3);
}

/* === Points lumineux === */
.job::before {
  content: "";
  position: absolute;
  top: 25px;
  width: 18px;
  height: 18px;
  background: #00bcd4;
  border-radius: 50%;
}

/* === Alternance gauche/droite === */
.job:nth-child(odd) {
  left: 50%;
}

.job:nth-child(odd)::before {
  right: -9px;
}

.job:nth-child(even) {
  right: 0;
}

.job:nth-child(even)::before {
  left: -9px;
}

/* === Titres et contenus === */
.job h4 {
  color: #00bcd4;
  font-size: 1.15rem;
  margin-bottom: 8px;
  font-weight: 600;
}

.job p {
  font-size: 0.9rem;
  color: #bbb;
  margin-bottom: 12px;
  font-style: italic;
}

.job ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.job li {
  color: #e5e5e5;
  line-height: 1.6;
  font-size: 0.95rem;
  position: relative;
  padding-left: 18px;
  margin-bottom: 8px;
}

.job li::before {
  content: "‚ñπ";
  color: #00bcd4;
  position: absolute;
  left: 0;
  top: 0;
}

/* === Responsive === */
@media (max-width: 768px) {
  .experience::before {
    left: 12px;
  }

  .job {
    width: 100%;
    left: 0 !important;
    padding-left: 35px;
  }

  .job::before {
    left: -2px !important;
  }
}


.formations {
  position: relative;
  padding: 60px 20px;
  color: #fff;
}

/* ===== Titre ===== */
.formations h3 {
  text-align: center;
  font-size: 2rem;
  color: #00bcd4;
  text-transform: uppercase;
  margin-bottom: 60px;
  letter-spacing: 1px;
  position: relative;
}

.formations h3::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  width: 70px;
  height: 3px;
  background: #00bcd4;
  transform: translateX(-50%);
  border-radius: 3px;
}

/* ===== Timeline verticale ===== */
.formations::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  width: 3px;
  height: 100%;
  background: rgba(255, 255, 255, 0.08);
  transform: translateX(-50%);
}

/* ===== Cartes de formation ===== */
.formation {
  position: relative;
  width: 50%;
  padding: 25px 35px;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  margin-bottom: 50px;
  backdrop-filter: blur(6px);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.formation:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 15px rgba(0, 188, 212, 0.25);
}

/* Points sur la ligne */
.formation::before {
  content: "";
  position: absolute;
  top: 25px;
  width: 14px;
  height: 14px;
  background: #00bcd4;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 188, 212, 0.4);
}

/* Alternance gauche-droite */
.formation:nth-child(odd) {
  left: 0;
}

.formation:nth-child(odd)::before {
  right: -7px;
}

.formation:nth-child(even) {
  left: 50%;
}

.formation:nth-child(even)::before {
  left: -7px;
}

/* ===== Contenu ===== */
.formation h4 {
  color: #00bcd4;
  font-size: 1.1rem;
  margin-bottom: 5px;
}

.formation p {
  font-size: 0.9rem;
  color: #bbb;
  margin-bottom: 10px;
  font-style: italic;
}

.formation ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.formation li {
  color: #eee;
  line-height: 1.6;
  font-size: 0.95rem;
  position: relative;
  padding-left: 15px;
  margin-bottom: 8px;
}

.formation li::before {
  content: "‚ñπ";
  color: #00bcd4;
  position: absolute;
  left: 0;
  top: 0;
}

/* ===== Responsive ===== */
@media (max-width: 768px) {
  .formations::before {
    left: 10px;
  }

  .formation {
    width: 100%;
    padding-left: 30px;
    left: 0 !important;
  }

  .formation::before {
    left: 0;
  }
}

.about h3 {
