.diplomes {
  padding: 80px 20px;
  background: radial-gradient(circle at top left, #0f172a, #1e293b);
  color: #fff;
  text-align: center;
}

.diplomes h3 {
  font-size: 2rem;
  color: #ffd166;
  text-transform: uppercase;
  margin-bottom: 50px;
  position: relative;
  letter-spacing: 1px;
}

.diplomes h3::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: #ffd166;
  border-radius: 3px;
}

/* === Cartes de dipl√¥me === */
.diplome {
  max-width: 900px;
  margin: 0 auto 40px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  padding: 30px 40px;
  text-align: left;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.25);
  transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
  backdrop-filter: blur(6px);
}

.diplome:hover {
  transform: translateY(-6px);
  border-color: rgba(255, 209, 102, 0.4);
  box-shadow: 0 0 25px rgba(255, 209, 102, 0.25);
}

/* === Titres === */
.diplome h4 {
  font-size: 1.2rem;
  color: #ffd166;
  margin-bottom: 8px;
}

.diplome p {
  font-size: 0.95rem;
  color: #bbb;
  margin-bottom: 15px;
  font-style: italic;
}

/* === Liste === */
.diplome ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.diplome li {
  position: relative;
  font-size: 0.95rem;
  line-height: 1.7;
  color: #e5e5e5;
  padding-left: 18px;
}

.diplome li::before {
  content: "üéì";
  position: absolute;
  left: 0;
  top: 0;
  font-size: 0.8rem;
  color: #ffd166;
}

/* === Responsive === */
@media (max-width: 768px) {
  .diplome {
    padding: 25px 20px;
  }

  .diplomes h3 {
    font-size: 1.6rem;
  }

  .diplome h4 {
    font-size: 1.05rem;
  }
}



