.competences {
  padding: 80px 20px;
  background: linear-gradient(135deg, #0f172a, #1e293b);
  color: #fff;
  text-align: center;
}

.competences h3 {
  font-size: 2rem;
  color: #ffd166;
  text-transform: uppercase;
  margin-bottom: 50px;
  letter-spacing: 1px;
  position: relative;
}

.competences h3::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: #ffd166;
  border-radius: 3px;
}

/* === Grille des cat√©gories de comp√©tences === */
.competences-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 30px;
  max-width: 1100px;
  margin: 0 auto;
}

/* === Carte de comp√©tence === */
.competence-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  padding: 30px 40px;
  text-align: left;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(6px);
  transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
}

.competence-card:hover {
  transform: translateY(-6px);
  border-color: rgba(255, 209, 102, 0.4);
  box-shadow: 0 0 25px rgba(255, 209, 102, 0.25);
}

/* === Titre de la cat√©gorie === */
.competence-card h4 {
  font-size: 1.2rem;
  color: #ffd166;
  margin-bottom: 15px;
}

/* === Liste de comp√©tences === */
.competence-card ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.competence-card li {
  position: relative;
  font-size: 0.95rem;
  line-height: 1.7;
  color: #e5e5e5;
  padding-left: 22px;
  margin-bottom: 8px;
}

.competence-card li::before {
  content: "‚öôÔ∏è";
  position: absolute;
  left: 0;
  top: 0;
  font-size: 0.85rem;
  color: #ffd166;
}

/* === Responsive === */
@media (max-width: 768px) {
  .competences h3 {
    font-size: 1.6rem;
  }

  .competence-card {
    padding: 25px 20px;
  }
}