.contact {
  padding: 80px 20px;
  background: linear-gradient(135deg, #0f172a, #1e293b);
  color: #fff;
  text-align: center;
}

.contact h3 {
  font-size: 2rem;
  color: #ffd166;
  text-transform: uppercase;
  margin-bottom: 50px;
  letter-spacing: 1px;
  position: relative;
}

.contact h3::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: #ffd166;
  border-radius: 3px;
}

/* === Carte de contact === */
.rs {
  max-width: 750px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  padding: 40px 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(6px);
  transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
}

.rs:hover {
  border-color: rgba(255, 209, 102, 0.4);
  box-shadow: 0 0 25px rgba(255, 209, 102, 0.25);
}

/* === Liens de contact === */
.rs a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 12px 20px;
  background: rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  text-decoration: none;
  color: #e5e5e5;
  font-size: 1rem;
  transition: background 0.3s ease, transform 0.3s ease, color 0.3s ease;
}

.rs a:hover {
  background: #ffd166;
  color: #0f172a;
  transform: translateY(-3px);
  font-weight: 600;
}

/* === Responsive === */
@media (max-width: 768px) {
  .contact h3 {
    font-size: 1.6rem;
  }

  .rs {
    padding: 25px 20px;
    gap: 15px;
  }

  .rs a {
    font-size: 0.95rem;
    padding: 10px 16px;
  }
}
