.about {
  padding: 80px 20px;
  background: linear-gradient(135deg, #0f172a, #1e293b);
  color: #fff;
  text-align: center;
}

.about h3 {
  font-size: 2rem;
  color: #ffd166;
  text-transform: uppercase;
  margin-bottom: 50px;
  letter-spacing: 1px;
  position: relative;
}

.about h3::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: #ffd166;
  border-radius: 3px;
}

/* === Contenu encadr√© (style carte comme comp√©tences) === */
.about-content {
  max-width: 850px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  padding: 40px 50px;
  text-align: left;
  line-height: 1.8;
  font-size: 1.05rem;
  color: #e5e5e5;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(6px);
  transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
}

.about-content:hover {
  transform: translateY(-5px);
  border-color: rgba(255, 209, 102, 0.4);
  box-shadow: 0 0 25px rgba(255, 209, 102, 0.25);
}

/* === Mise en valeur de mots-cl√©s === */
.about-content strong,
.about-content span.highlight {
  color: #ffd166;
  font-weight: 600;
}

/* === Responsive === */
@media (max-width: 768px) {
  .about-content {
    padding: 25px 20px;
    font-size: 0.95rem;
  }

  .about h3 {
    font-size: 1.6rem;
  }
}
