.projets-section {
  text-align: center;
  color: #fff;
}

.projets-section h3 {
  font-size: 1.8rem;
  margin-bottom: 30px;
  color: #ffcc00;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
}

.projets-section h3::after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background: #ffcc00;
  margin: 10px auto 0;
  border-radius: 3px;
}

/* ====== Grille des projets ====== */
.projets {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  justify-content: center;
  padding: 20px;
}

/* ====== Style des liens ====== */
.projet-link {
  display: block;
  padding: 14px 18px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(6px);
  color: #fff;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
  opacity: 0;
  transform: translateY(15px);
  animation: fadeInUp 0.5s forwards;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

/* ====== Hover ====== */
.projet-link:hover {
  background: rgba(255, 204, 0, 0.15);
  color: #ffcc00;
  transform: translateY(-4px);
  border-color: rgba(255, 204, 0, 0.3);
  box-shadow: 0 0 10px rgba(255, 204, 0, 0.3);
}

/* ====== Animation d‚Äôapparition ====== */
@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ====== Responsive ====== */
@media (max-width: 768px) {
  .projets-section h3 {
    font-size: 1.4rem;
  }

  .projets {
    gap: 15px;
    padding: 10px;
  }

  .projet-link {
    font-size: 0.9rem;
    padding: 12px;
  }
}
